import React from 'react'; // ,{useContext}
import posts from './../data/posts';
import {Link} from 'react-router-dom';
//import {contextoTema} from "../contextos/ContextoTema";
import Controles from "./Controles";

const Blog = () => {
	//const nombrecontextoTema = useContext(contextoTema);	
	//console.log(nombrecontextoTema); //mostramos el objeto recibido
	//console.log(nombrecontextoTema.nombre);
	//const {nombre} = useContext(contextoTema); // también podemos extraer el nombre directamente de esta manera
	//console.log(nombre);
	return (
		<div>
			<h2>Blog</h2>
			<ul>
				{posts.map((post) => {
					return (
						<li key={post.id}>
							<Link to={`/post/${post.id}`}>{post.titulo}</Link>
						</li>
					)
				})}
			</ul>
			<Controles/>
		</div>
	);
}

export default Blog;