import React, { useState } from 'react';
/*  <-- Este guardará la configuración global de nuestro tema */

// creamos cntexto = estado global 
const contextoTema = React.createContext();

const ProveedorTema = ({children}) => {
    //tema el estado tema contendrá todas las configuraciones del tema como tamaño de fuente y alineado 
    const [tema, cambiarTema] = useState({
        alineado: "center",
        fuente: 20
    });
    const aumentarFuente = ()=> {
        cambiarTema({
            ...tema,
            fuente: tema.fuente +1
        })
        }

    const disminuirFuente = () => {
        cambiarTema({
            ...tema,
            fuente: tema.fuente -1            
        });
    }

    const alineadoIzquierda = () =>{
        cambiarTema({
            ...tema,
            alineado: "left"
        });
    }

    const alineadoCentro = () => {
        cambiarTema({
            ...tema,
            alineado: "center"
        });
    }

    const alineadoDerecha = () => {
        cambiarTema({
            ...tema,
            alineado: "right"
        });
    }
    return (
        <contextoTema.Provider value={ {tema,aumentarFuente,disminuirFuente,alineadoIzquierda,alineadoCentro,alineadoDerecha} }> {/*{nombre:"Carlos"}//De esta manera le inyectamos las funciones a los hijos*/}
            {children}
        </contextoTema.Provider>
    )
}

export {ProveedorTema,contextoTema}; 