import React, {useContext}from 'react'; // useContext para conectarnos a contextoTema
import styled from 'styled-components';
import {contextoTema} from "./../contextos/ContextoTema";

const Controles = () => {
    const {aumentarFuente} = useContext(contextoTema); // extraemos solo la función aumentarFuente de contextoTema
    const {disminuirFuente,alineadoIzquierda,alineadoCentro,alineadoDerecha} = useContext(contextoTema); // podemos extraer en una sola línea para no repetir

    //console.log(aumentarFuente);
    return (  
        <ContenedorControles>
            <div>
                <Boton onClick={aumentarFuente}>Aumentar Fuente</Boton>
                <Boton onClick={disminuirFuente}>Disminuir Fuente</Boton>
            </div>
            <div>
                <Boton onClick={alineadoIzquierda}>Izquierda</Boton>
                <Boton onClick={alineadoCentro}>Centro</Boton>
                <Boton onClick={alineadoDerecha}>Derecha</Boton>
            </div>
        </ContenedorControles>
    );
}

const ContenedorControles = styled.div`
    margin-top: 20px;
`;


const Boton = styled.button`
    background: #165168;
    color: #fff;
    border: none;
    cursor: pointer;
    font-size: 12px;
    padding: 7px;
    margin-right: 10px;
    margin-bottom: 10px;
    border-radius: 3px;

    &:hover {
        background: #191668;
    }
`;


export default Controles;